import '../../frame/frame.js';
import './login.less';

var app = angular.module('loginApp', []);
app.controller('loginCtrl', ['$scope', '$window', function ($scope, $window) {
    $scope.loginData = GLOBAL_LOGIN;
    $scope.login = {
        name: '',
        password: ''
    };
    $scope.clearName = function () {
        $scope.login.name = '';
    };
    $scope.clearPassword = function () {
        $scope.login.password = '';
    };
    //登录
    $scope.loginSubmit = function () {
        //验证账号密码

        //跳转页面
        $window.location.href = $scope.loginData.indexUrl;
    };
}]);
app.run(['$templateCache', function ($templateCache) {
    let strTem = require('./login.html');
    $templateCache.put('login.tpl.html', strTem);
}]);

angular.bootstrap(document, ['loginApp']);



